var ChartsPlugin = function ChartsPlugin(md) {
  var tokenInfo = {
    CHART: 'chart',
    ECHARTS: 'echarts',
    HIGHCHARTS: 'highcharts',
    CHARTIST: 'chartist',
    C3: 'c3',
    TAUCHARTS: 'taucharts'
  };
  var temp = md.renderer.rules.fence.bind(md.renderer.rules);

  if (Object.freeze) {
    Object.freeze(tokenInfo);
  }

  md.renderer.rules.fence = function (tokens, idx, options, env, slf) {
    var token = tokens[idx];
    var code = token.content.trim();

    if (Object.keys(tokenInfo).some(function (key) {
      return token.info === tokenInfo[key];
    })) {
      try {
        var json = JSON.parse(code);

        switch (token.info) {
          case tokenInfo.CHART:
            return "<canvas class=\"chartjs\">".concat(JSON.stringify(json), "</canvas>");
            break;

          case tokenInfo.ECHARTS:
            return "<div class=\"echarts\"></div><div class=\"echarts-data\" style='display:none'>".concat(JSON.stringify(json), "</div>");
            break;

          case tokenInfo.HIGHCHARTS:
            return "<div class=\"highcharts\"><div class=\"highcharts-data\" style='display:none'>".concat(JSON.stringify(json), "</div></div>");
            break;

          case tokenInfo.CHARTIST:
            return "<div class='ct-chart ct-golden-section'></div><div class='chartist-data' style='display:none'>".concat(JSON.stringify(json), "</div>");
            break;

          case tokenInfo.C3:
            return "<div class='c3-chart' id=".concat(json.id, "></div><div class='c3-data' style='display:none'>").concat(JSON.stringify(json), "</div>");
            break;

          case tokenInfo.TAUCHARTS:
            return "<div class='taucharts' id=".concat(json.id, "></div><div class='taucharts-data' style='display:none'>").concat(JSON.stringify(json), "</div>");
            break;

          default:
            break;
        }
      } catch (err) {
        return "<pre>".concat(err, "</pre>");
      }
    }

    return temp(tokens, idx, options, env, slf);
  };
};

module.exports = ChartsPlugin;